export default ({ fields, progress }) => {
  const values = Object.keys(fields).map(key => fields[key]);

  if (values.includes('')) {

    return { status: false, 
      error: 'empty', 
      errorInputs: Object.keys(fields).filter(key => fields[key] === ''), 
      msg: '   '}
  } else if (progress === 'access_error') {

    return { status: false, 
      error: 'access',
      errorInputs: ['login', 'password'],
      msg: '   ' }
  } else {
    
    return { status: true, 
      error: '', 
      errorInputs: [],
      msg: ''}
  }
}